<template>
  <div id="app">
    <div class="top">
      <img src="./assets/03.png" />
    </div>
    <div class="mid"> 
      <div class="day">{{dat}}</div>
      <div class="time">{{nowtime}}</div>
      <img alt="Vue logo" src="./assets/02.png"/>
    </div>
    <div class="bot">
      <img src="./assets/04.png"/>
    </div>
  </div>
</template>

<script>

export default {
  name: 'App',
  components: {
  },
  data() {
    return {
      timer: "", //定义一个定时器的变量
      currentTime: '' ,// 获取当前时间
      nowtime:"",
      
    }
  },
  computed:{
    dat:function(){
      let mon=(new Date().getMonth()+1)
      if(mon<10){mon="0"+(new Date().getMonth()+1)}
			let dat = (new Date().getFullYear())+"年"+mon+"月"+(new Date().getDate())+"日"
      return dat
    }
  },
  created(){
    setInterval(()=>{
					//new Date() new一个data对象，当前日期和时间
					//toLocaleString() 方法可根据本地时间把 Date 对象转换为字符串，并返回结果。
          let ms=(new Date().getMilliseconds()).toLocaleString().slice(2)
					this.nowtime = (new Date().toLocaleString().slice(9))+"."+ms
				},200)
  }
}
</script>

